@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#f1efea}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}*{box-sizing:border-box}.drop-container{cursor:pointer}.drop-container:focus{outline:none}

.App-landing-section{align-items:center;background-color:#282c34;border:5px solid transparent;color:#fff;display:flex;flex-direction:column;font-size:calc(10px + 2vmin);justify-content:center;min-height:100vh;text-align:center}.App-landing-section a{color:#61dafb;text-decoration:none}.App-landing-section a:hover,.App-landing-section a:active,.App-landing-section a:focus{color:#fff}

.Section{background-color:#fff;border-bottom:2px solid rgba(0,0,0,0.15);border-radius:2px;margin:10px auto;max-width:980px}.Section p{margin:0 0 1em 0}.Section-heading{padding:20px;border-bottom:1px solid #e6e6e6;font-size:1em;text-transform:uppercase;word-break:break-word;font-weight:bold}.Section-content{overflow-wrap:break-word;padding:20px;word-break:break-word}h3 .Anchor{display:none}h3:hover .Anchor{display:inline}.Tab-count{border-radius:3.75em;color:#fff;background:#287fb8;display:inline-block;font-size:0.8em;font-weight:bold;line-height:1;margin-left:10px;padding:4px 7px;vertical-align:1px}.Tabs{margin:0;padding:0;list-style:none;padding:10px 20px}.Tabs li{display:inline-block;margin:10px 30px 10px 0;vertical-align:middle}.Tabs li a{background:none;border:0;border-bottom:2px solid transparent;color:#287fb8;cursor:pointer;font-size:1em;margin:0;outline:none;padding:5px 0}.Tabs li a.is-active,.Tabs li a:hover,.Tabs li a:focus{color:#ed6504}.Tabs li a.is-active .Tab-count,.Tabs li a.is-active .Tab-count,.Tabs li a.is-active .Tab-count,.Tabs li a:hover .Tab-count,.Tabs li a:hover .Tab-count,.Tabs li a:hover .Tab-count,.Tabs li a:focus .Tab-count,.Tabs li a:focus .Tab-count,.Tabs li a:focus .Tab-count{background:#ed6504}.Tabs li a.is-active{border-color:rgba(0,0,0,0.15)}

.About-FAQ {
  max-width: 780px;
  margin: 0 auto 100px auto;
  padding: 30px;
  text-align: left;
}

.About-FAQ h3 {
  margin-top: 2.5em;
}

.Intro h1{font-size:2em;font-weight:300;margin:10px 40px}.Intro h2{font-size:1em;font-weight:300;margin:10px 40px}a.Intro-by-link{font-size:0.5em;color:#a9a9a9}.Intro-logo{height:30vmin;margin:30px;pointer-events:none}.Intro-instructions{max-width:700px}@media (prefers-reduced-motion: no-preference){.Intro-logo{animation:Intro-logo-spin infinite 20s linear}.is-processing .Intro-logo,.is-dropping .Intro-logo{animation:Intro-logo-spin infinite 2s linear}}.is-dropping .Intro{background-color:#363c48;border-color:#f1cb85}@keyframes Intro-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.Profile{background:#f1efea;border:5px solid transparent;color:#27374c;font-family:Lato, 'Helvetica Neue', Helvetica, Arial, 'Lucida Grande', sans-serif;font-feature-settings:'kern', 'liga', 'pnum';line-height:1.5;min-height:100vh;padding:50px}.Profile-header{margin:10px auto;max-width:980px}.Profile a{color:#287fb8;text-decoration:none}.Profile a:active,.Profile a:focus,.Profile a:hover{color:#ed6504;text-decoration:none}.is-dropping .Profile{background-color:#fdf7e7;border-color:#ed6504}.is-dropping .Profile *{opacity:0.8}.promo{text-align:center;font-size:1.5em;font-weight:300;margin-top:40px}.promo .tr-logo{width:50px}a.Profile-clear{background:transparent;border:2px solid rgba(0,0,0,0.15);cursor:pointer;float:right;padding:5px 10px}a.Profile-clear:active,a.Profile-clear:hover,a.Profile-clear:focus{border-color:#ed6504;color:#ed6504}

.AboutMe-interests{margin:10px 0;padding:0;list-style:none}.AboutMe-interests li{background-color:transparent;border-radius:4px;border:2px solid #bfcad2;color:#34495e;display:inline-block;font-size:16px;font-weight:bold;margin:4px;max-width:none;max-width:initial;min-width:0;min-width:initial;padding:2px 10px;width:auto}

.Image {
  max-width: 100%;
}

.Message{padding:30px 0;border-top:1px solid #e6e6e6}.Message-meta{display:flex;justify-content:space-between}.Message-date{color:#929eae;font-size:0.9em}.Message-preview{max-height:100px;overflow:hidden;position:relative}.Message-preview::after{background:linear-gradient(0deg, #fff 0%, rgba(255,255,255,0) 100%);bottom:0;content:'';height:30px;position:absolute;width:100%}

.Profile-reference{padding:30px 0;border-top:1px solid #e6e6e6}.Profile-reference-meta{display:flex;justify-content:flex-start}.Profile-reference-meta .Profile-reference-type{margin-right:5px}.Profile-reference-meta .Profile-reference-experience{margin-left:20px}.Profile-reference-meta .Profile-reference-date{margin-left:auto}.Profile-reference-experience{font-weight:bold}.Profile-reference-experience-positive{color:#41b866}.Profile-reference-experience-neutral{color:#929dad}.Profile-reference-experience-negative{color:#c63d43}.Profile-reference-date{color:#929eae;font-size:0.9em}.Profile-reference-response{margin-left:30px;border:1px solid rgba(0,0,0,0.15);margin-top:1em;padding:10px}

